h1,h2,h3,h4,h5,h6 {
    font-family: var(--font-family-roboto);
    padding: 0;
    margin: 10px;
}

.search-bar-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    gap: 1rem;
}

#search-headline {
    text-align: center;
}

#search-input-form {
    display: flex;
    gap: 5px;
    margin-top: 2rem;
    padding: 0;
}

#search-helper-text {
    margin: 0;
    padding: 0;
}

#gallery-container {
    display: flex;
    flex-wrap: wrap;
}

@media(hover: hover) and (pointer: fine) {
    .image-container:hover .image-details {
        visibility: visible;
    }
}

.image-container {
    display: flex;
    position: relative;
}

.image-container > img {
    width: 25vw;
    height: 35vw;
    object-fit: cover;
}

@media screen and (max-width: 600px) {
    .image-container img {
        display: flex;
        width: 50vw;
        height: 50vw;
    }
}

.image-details {
    display: flex;
    flex-direction: column;
    position: absolute;
    justify-content: space-around;
    align-items: center;
    visibility: hidden;
    color: white;
    background-color: rgba(0,0,0,0.70);
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: var(--font-family-roboto);
}

#touch-icon {
    position: absolute;
}

.image-container a:hover {
    text-decoration: underline;
}

.image-container a {
    color: white;
    text-align: center;
    text-decoration: none;
}

#powered-by-box {
    display: flex;
    justify-content: center;
}

#pexels-logo {
    width: 100px;
    height: auto;
}